import React, { Component, Fragment } from 'react';
import './Search.scss';

class Search extends Component { 
    constructor(props) {
        super(props);   
        this.state = {
            keywords : ['Do we have pricing for ACME Company?','ACME Company vs. ABCD Ltd.', 'How do we win against ACME Company?', 'Depositioning Battlecard for ACME Company'],
            keyword : props.keyword
        }    
    }
    componentDidMount () {
        // ready
        let _cnt = 0;
        
        setInterval(function(){
            this.animateKeyword(this.state.keywords[_cnt]);
            _cnt++;
            if (_cnt >= this.state.keywords.length)
                _cnt = 0;
        }.bind(this), 2500);
    }
    animateKeyword = (msg) => {
        let _search = this.refs.searchValue,
            _text = msg.split('');
        
        if (!_search) return;
        
        _search.innerHTML = ' ';
        
        _text.forEach(function (letter, index) {
            setTimeout(function(){
                _search.innerHTML = _search.innerHTML + letter;
            }, 20 * index);
        });
    }
    render () {
        return (
            <div class="Search">
                <div class="Search--box"><span ref='searchValue'><i>Ask Klue anything about your competitors!</i></span></div><button>search</button>
            </div>
        );
    }
}

export default Search;