import React, { Fragment, Component } from 'react';
import './Cards.scss';

let CARDS = [
    {
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "OVERVIEW"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "CHART"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "CHART"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "CHART"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "SMALL"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "MEDIUM"
    },{
        style : {top: '40vh', left:'calc(50vw - (100%/7)/2)'},
        type : "CHART"
    }
]

class Lanes extends Component { 
    constructor(props) {
        super(props);   
        this.state = {
            columns : 7,
            cards : CARDS,
            mode : props.mode // start, board, stack, battlecard
        }    
    }
    componentDidMount () {
        // ready
        //setTimeout(function(){this.handleStart()}.bind(this), 100);    
        //this.handleStart()   
    }
    componentDidUpdate(prevProps) {
        if (prevProps.mode !== this.props.mode) {
            this.handleMode();
            this.setState({
                mode : this.props.mode
            })
        }
    }
    handleStart = () => {
        let _cards = this.state.cards,
            _width = document.getElementById(`card-0`).clientWidth,
            _height = document.getElementById(`card-0`).clientHeight,
            _left = (window.innerWidth/2) - (_width/2),
            _top =  (window.innerHeight/2) - (_height/2);
        
        _cards.map((card, index) => {
            let _style = {}
            _style = {
                left : `${_left}px`,
                top : `${_top}px`
            };
            card.style = _style
        })
        this.setState({
            cards : _cards
        })
    }
    handleMasonry = () => {
        let _cards = this.state.cards,
            _width = document.getElementById(`card-0`).clientWidth,
            _columns = this.state.columns,
            _row = 0; // current row...
        
        _cards.map( (card, index) => {
            let _left = (index % _columns) * _width,
                _top = 0;

            for (let currentRow = 0; currentRow < _row; currentRow++) {
                let _f = _columns * (_row - currentRow);
                _top = _top + document.getElementById(`card-${index - (_f)}`).clientHeight;
            }                
            
            if ((index % _columns) === (_columns - 1)) {
                _row++;
            }

            card.style = {
                left : `${_left}px`,
                top : `${_top}px`
            };
            card.class = 'on';
            
            return card;
        })

        this.setState({
            cards : _cards
        })
    }
    handleStack = () => {
        let _cards = this.state.cards,
            _width = document.getElementById(`card-0`).clientWidth,
            _height = document.getElementById(`card-0`).clientHeight,
            _left = (window.innerWidth/2) - (_width/2) - 40,
            _top =  (window.innerHeight/2) - (_height/2) - 50 ;
        
        //let _stack = _cards.filter(card => card.type != 'MEDIUM');
        //_cards.sort((a, b) => (a.type > b.type) ? 1 : -1)
        _cards.map((card, index) => {
            
            let _style = {},
                _class = null;

            if (card.type === 'MEDIUM') {
                _style = {
                    left : card.style.left,
                    top : card.style.top
                }
                _class = 'off';
                
            } else {
                _left = _left + 5;
                _top = _top + 5;
                _style = {
                    left : _left + _width/2 + "px",
                    top : `${_top}px`,
                };
            }
            
            if (card.type === 'OVERVIEW') {
                _style = {
                    left : _left - _width/2 + "px",
                    top : `${_top}px`
                }
            }
            card.style = _style;
            card.class = _class;
            
            return card;
        });
        this.setState({
            cards : _cards
        })
    }
    handleBattlecard = () => {
        let _cards = this.state.cards,
            _width = document.getElementById(`card-0`).clientWidth,
            _height = document.getElementById(`card-0`).clientHeight,
            _columns = 4,
            _cnt = 0; // current row...
        
        let _offsetTop = window.innerHeight/2 - _height,
            _offsetLeft = window.innerWidth/2 - (_width * 2);
        
        _cards.map((card, index) => {
            let _left = 0,
                _top = 0,
                _style = {};
            
            if ((card.class != 'off') && (card.type != 'MEDIUM')) {
                _left = (_cnt % _columns) * _width;
                _top = (_cnt >= 4) ? _height : 0;
                
                _style = {
                    left : `${_offsetLeft + _left}px`,
                    top : `${_offsetTop + _top}px`
                }
                
                _cnt ++;
            }
            
            if ((card.type === 'MEDIUM')) { 
                _style = {
                    left : card.style.left,
                    top : card.style.top
                }
                card.class = 'off';
            }
            
            card.style = _style;
            
            return card;
        })
        this.setState({
            cards : _cards
        })
    }
    handleMode = () => {
        switch(this.props.mode) { 
           case 'board': { 
                //this.handleMasonry();
                setTimeout(function(){this.handleMasonry()}.bind(this), 2000);
                break; 
           } 
           case 'stack': { 
                this.handleStack();
                break; 
           }
           case 'battlecard': { 
                this.handleBattlecard();
                break;    
           }
           case 'search': { 
                this.handleMasonry()
                break;    
           }
           case 'done': { 
                this.handleStart();
                break;    
           }
           default: { 
                console.log("Invalid choice"); 
                break;              
           } 
        } 
    }

    render () {
        return (
            <div class={`Cards ${this.state.mode}`}>
                { 
                    this.state.cards.map( (card, index) => 
                        <div 
                            id={ `card-${index}` }
                            class={ `Card ${card.class}` } 
                            style={ card.style }
                        >
                            <img src={ "/assets/" + card.type + ".svg" } />
                        </div>
                    )
                }
            </div>
        );
    }
}
export default Lanes;