import React, { Component, Fragment } from 'react';
import Banner from './components/Banner/Banner';
import Tiles from './components/Tiles/Tiles';
import Cards from './components/Cards/Cards';
import Search from './components/Search/Search';
import Cursor from './components/Cursor/Cursor';

// data
import BANNERS from "./_clickable.002.js"; 
// alert versions

// non-clickable w/search: _non-clickable.001.js
// clickable w/search: _clickable.001.js

// non-clickable no search: _non-clickable.002.js

// clickable w/search re-ordered: _clickable.002.js

// styles
import "./styles.scss";

class App extends Component { 
    constructor(props) {
        super(props);   
        this.state = {
            banners : BANNERS,
            step : 0
        }    
    }
    componentDidMount () {
        // ready
        document.addEventListener('mousemove', this.handleMouseMove);
        document.addEventListener('mousedown', 
            function(){
                this.setState({ cursorClass : 'hide'})
                
            }.bind(this));
        document.addEventListener('keydown', function(e){
            if (e.keyCode === 32) {
                this.handleNextStep();
            }
        }.bind(this))
        setInterval(function(){
            this.setState({ cursorClass : 'hide'})
        }.bind(this), 8000);
        
    }
    handleNextStep = () => {
        this.setState({
            step : (this.state.step >= (this.state.banners.length - 1)) ? 0 : this.state.step + 1
        })
    }
    handleMouseMove = (e) => {
        
        this.setState({
            cursorStyle : {
                top : e.pageY - 22 + "px",
                left : e.pageX - 22 + "px",
                
            },
            cursorClass : e.target.classList.contains('Banner') ? 'on' : 'off'
        })
    }
    render () {
            
        let _banner = this.state.banners[this.state.step],
            _mode = this.state.banners[this.state.step].class;
        return (
            <main class={ _mode }>
                <Cursor class={ this.state.cursorClass } style={ this.state.cursorStyle } />
                <Banner
                    onClick={ this.handleNextStep }
                    mode={ _mode }
                    banner={ _banner }
                />
                <Cards mode={ _mode } />
                { _mode === 'search' && <Search /> }
                { 
                    //(_mode === 'init' || _mode === 'board') && <Tiles /> 
                    <Tiles />
                }
                
            </main>
        );
    }
}

export default App;