import React, { Component } from 'react';
import './Tiles.scss';

let TILES = ["Apple.com","Audi.com","Facebook.com","Slack.com","Amazon.com","BP.com","Cisco.com","Google.ca","Microsoft.com","BMW.com","Citigroup.com","Instagram.com","Unilever.com","Sony.com","Klue.com"];


const Tiles = (props) => 
    <section class="Tiles">
        { TILES.map( tile =>
            <div class="Tile">
                <div>
                    <img src={`https://logo.clearbit.com/${tile.toLowerCase()}`} />
                    <h4>{ tile.split('.')[0] }</h4>
                </div>
            </div>
        )}
    </section>

export default Tiles;